<template>
    <div class="playpage">
        <div class="top">
            <img class="bg"   src="../images/f-day_bg_question.png">
            <p class="desc">{{desc}}</p>
        </div>
        <div  id='wan' @click="begin" class="play moveBottom">
            <transition-group  name="slide-fade" >
                <div class="cont" v-for="(li,key) in timu "  :key="key" ><span>{{li}}</span></div>
            </transition-group>
        </div>
        <p  class="bottom">·{{show_page}}/7·</p>
        <div id="b"></div>
    </div>
</template>

<script>
    export default {
        name: "PlayPage",
        data(){
            return {
                desc: '1.你是爸爸还是儿子？',
                timu: ['A.乖儿子', 'B.小公主'],
                page: 1,
                show_page:1,
                man_timu_list: ['2.小时候哪件事情，让老爸最生气！','3.长大之后，最让老爸操心的事情是？', '4.你印象中，被揍得最惨的事情是？', '5.把老爸坑得最惨的一次是？', '6.你觉得自己最擅长的科目是？', '7.你会选哪种游戏，和老爸互动？' ],
                man: [{
                    0: 'A.我是老实人，老爸从没生气。',
                    5:'B.欺负女同学，被老师罚留堂。',
                    25:'C.偷钱买游戏机，被老爸发现。',
                    15:'D.青春期叛逆，谈恋爱被发现。'
                },{
                    1:'A.学习成绩差',
                    5:'B.工作不稳定',
                    20:'C.没恋爱/没结婚',
                    30:'D.没钱买房结婚'
                },{
                    0:'A.作业不会做',
                    10:'B.和同学打架',
                    25:'C.不孝敬长辈',
                    6:'D.去黑网吧玩'
                },{
                    40:'A.帮妈妈找到老爸的私房钱',
                    5:'B.让长辈知道自己被爸爸打',
                    10:'C.把爸爸做的坏事告诉妈妈',
                    20:'D.外人面前拆穿爸爸的谎话'
                },{
                    1:'A.语文',
                    10:'B.数学',
                    5:'C.英语',
                    20:'D.理综'
                },{
                    20:'A.手游：吃鸡/王者荣耀',
                    10:'B.互动：打麻将/喝酒猜码',
                    5:'C.打牌：斗地主/争上游',
                    1:'D.下棋：象棋/围棋'
                }],
                woman_timu_list: ['2.提到作业/项目，你第一反应是？', '3.和长辈吃饭，你会？', '4.印象中，爸爸更像？', '5.未来带男朋友回家，你觉得爸爸会？', '6.你希望哪个角色做你老爸？', '7.爸爸批评你的时候，你内心是？'],
                woman:[{
                    10:'A.会害怕：惨了，肯定不会写！',
                    25:'B.会愤怒：别和我谈作业/项目！',
                    0:'C.很兴奋：终于有机会大显身手了！',
                    15:'D.不情愿：不是吧，又有作业/项目！'
                },{
                    15:'A.肯定是先拍照，发朋友圈！',
                    0:'B.为在场的每个人倒上饮料。',
                    5:'C.听长辈的安排，当透明人。',
                    30:'D.经常迟到，长辈也习惯了。'
                },{
                    3:'A.老师',
                    0:'B.朋友',
                    5:'C.超人',
                    20:'D.提款机'
                },{
                    5:'A.虽然话不多，但会默默观察男友。',
                    10:'B.必须喝两杯，在酒桌上侃侃而谈。',
                    0:'C.假装很凶悍，害怕以后你被欺负。',
                    15:'D.非常的激动，感谢终于有人要你。'
                },{
                    10:'A.Superman',
                    5:'B.大白',
                    0:'C.都不如自己的老爸',
                    25:'D.夜礼服假面'
                },{
                    5:'A.芝麻蒜皮的事，又开始高谈阔论了！',
                    10:'B.左耳进右耳出，继续做自己的主人！',
                    20:'C.找准时机争论，充分表达自己想法！',
                    0:'D虚心接受批评，自我检讨不足之处！'
                }],
                cont_timu:[],
                cont_desc:[],
                score:0,
                items:[1,2,3,4]
            }
        },
        methods:{
            test(){
               // const keys = Object.keys( this.timu )
               // console.log(keys)
               // Vue.delete(this.timu,keys[0])
                this.timu.pop()


                if(! (0 in this.timu) ) {
                    setTimeout(
                        ()=>{

                            const li = []
                            const keys = []

                             Object.values( this.cont_timu[this.page-2] ).find((n)=>{
                                 if(n.split('').includes('A')){
                                     li[0] = n
                                     keys[0] = _.findKey(this.cont_timu[this.page-2], function(o) { return o == n  })
                                 }else if(n.split('').includes('B')){
                                     li[1] = n
                                     keys[1] = _.findKey(this.cont_timu[this.page-2], function(o) { return o == n  })
                                 }else if(n.split('').includes('C')){
                                     li[2] = n
                                     keys[2] = _.findKey(this.cont_timu[this.page-2], function(o) { return o == n  })
                                 }else if(n.split('').includes('D')){
                                     li[3] = n
                                     keys[3] = _.findKey(this.cont_timu[this.page-2], function(o) { return o == n })
                                 }
                             })

                            console.log(keys)
                            console.log('上面的事分数')
                            this.timu = li


                            this.desc = this.cont_desc[this.page-2]
                            console.log('你麻麻？')
                            setTimeout(()=>{
                                for( let i = 0 ;i<4;i++ ){

                                    document.querySelectorAll(".cont")[i].setAttribute('score',keys[i])
                                    console.log('sb?')
                                    //li.setAttribute('key',this.cont_timu[this.page-2][index])
                                }
                            },500)

                        }
                    ,1500)
                }
            },
            next(e){

                if( !e.target.firstChild.firstChild ){ //子节点

                    setTimeout(()=>{
                        this.show_page += 1
                    },2200) //没事我懒
                    console.log(e.target.parentNode)
                    e.target.parentNode.classList.add('bg-change')
                    if(e.target.parentNode.getAttribute('score')){
                        console.log(   e.target.parentNode.getAttribute('score') +'分数！')
                        this.score += parseInt( e.target.parentNode.getAttribute('score') )
                    }

                    this.fade()
                }else if(  e.target.classList[0] == 'cont'  ){
                    setTimeout(()=>{
                        this.show_page += 1
                    },2200) //没事我懒 脑子懒
                    console.log(e.target)
                    e.target.classList.add('bg-change')
                    if(e.target.getAttribute('score')){
                        console.log(   e.target.getAttribute('score') +'分数！')
                        this.score += parseInt( e.target.getAttribute('score') )
                    }


                    this.fade()
                }

            },
            fade(){
                let count = 0
                const inter = setInterval(
                    ()=>{
                        this.$nextTick(()=>{
                            this.test()
                            count += 1
                            if(count === 4) clearInterval(inter)
                        })
                    },200)
            },
            begin(e){
                e.preventDefault()
                this.$nextTick(()=>{
                    this.debounce(e, this)
                })
            },
            sexChange(sex){
                const string = sex
                if( sex == 'A.乖儿子'){
                    localStorage.setItem('sex','man')
                    this.cont_desc = this.man_timu_list  //这里我有必要说一下timu 其实是下面的那四个题
                    this.cont_timu = this.man
                }else{
                    localStorage.setItem('sex','woman')
                    this.cont_desc = this.woman_timu_list
                    this.cont_timu = this.woman
                }
            },
            debounce:_.debounce((e, that)=>{
                if(1 in document.querySelector('#wan').classList){

                    //获取用户选择的是A还是B
                    if( !e.target.firstChild.firstChild ){ //子节点
                        console.log( e.target.innerHTML)
                        that.sexChange(e.target.innerHTML)
                    }else if(  e.target.classList[0] == 'cont'  ){
                        console.log( e.target.firstChild.firstChild +'asdasdasdasdads')
                        that.sexChange(e.target.firstChild.innerHTML)
                    }
                    //帮助动画更加流畅
                    setTimeout(()=>{
                        document.querySelector('#wan').classList.remove('moveBottom')
                    },2000)
                    that.$data.page += 1
                    that.next(e)
                }else{

                    if(!(that.page == 8)){
                        that.page += 1
                        that.next(e)
                    }
                    if(that.page == 8){
                        that.$nextTick(()=>{
                            //that.page -= 2
                            console.log(that.score+'我特么是分数啊')
                            const score = that.score
                            //that.fade()
                            localStorage.setItem('score',score)
                            setTimeout(()=>{
                                that.$router.push({path:'/result'})
                            },700)

                        })
                    }
                }
            },2300, {leading : true})
        }

    }
</script>

