<template>
    <div class="result">
        <div class="top">
            <div id="canvas">
                <img class="title" src="../images/zsd.png">
                <div class="center" :style="{backgroundColor: rgba}">
                    <img class="cn" src="../images/f-day_img_resultcard_pic-boy2.png">
                    <div class="miaoshu">
                        <p class="name">{{name}}</p>
                        <div class="fenshu-outer">坑爹指数:<span class="fenshu">{{score}}</span></div>
                        <div>坑爹段位:<p class="dw">{{dw}}</p></div>
                    </div>
                </div>
                <div class="by">{{by}}</div>
                <div class="wa">{{wa}}</div>
                <div class="bottom-desc">
                    <img  class='left' src="../images/f-day_img_openning-logo.png"/>
                    <img  class="right" src="../images/qrcode_wechat.png"/>
                    <span  class="sentance" >长按测试</span>
                </div>
            </div>
        </div>
        <p class="c">*长按保存*</p>
