                    case this.score>=5 && this.score <=7: this.dw = '青铜I';break;
                    case this.score>=8 && this.score <=12: this.dw = '白银III';break;
                    case this.score>=13 && this.score <=17: this.dw = '白银II';break;
                    case this.score>=18 && this.score <=20: this.dw = '白银I';break;
                    case this.score>=21 && this.score <=25: this.dw = '黄金IV';break;
                    case this.score>=26 && this.score <=30: this.dw = '黄金III';break;
                    case this.score>=31 && this.score <=35: this.dw = '黄金II';break;
                    case this.score>=36 && this.score <=40: this.dw = '黄金I';break;
                    case this.score>=41 && this.score <=45: this.dw = '铂金IV';break;
                    case this.score>=46 && this.score <=50: this.dw = '铂金III';break;
                    case this.score>=51 && this.score <=55: this.dw = '铂金II';break;
                    case this.score>=56 && this.score <=60: this.dw = '铂金I';break;
                    case this.score>=61 && this.score <=66: this.dw = '砖石V';break;
                    case this.score>=67 && this.score <=72: this.dw = '砖石IV';break;
                    case this.score>=73 && this.score <=78: this.dw = '砖石III';break;
                    case this.score>=79 && this.score <=84: this.dw = '砖石II';break;
