                    case this.score<=60:this.rgba =  'rgba(251,172,33,0.5)';break;
                    case this.score<=80:this.rgba =  'rgba(240,77,60,0.5)';break;
                    case this.score<=100:this.rgba = 'rgba(238,53,96,0.7)';break;
                }
            },
            getWA(){
                if(this.sex == 'woman'){//randomNum
                    switch(true){
                        case this.score>=0 && this.score<=7:   this.by = ['爸爸有你，平安如意！','乖宝宝，你没少被你爸坑！','你爸不脆弱，尽管坑！'][randomNum(0,2)];this.wa = ['你从小就是乖宝宝，只有被你爸坑的份，你很少会为老爸挖坑。毕竟不专业，挖着挖着，自己会掉进去！', '你善良、单纯，一直都是老爸的得力助手！老爸为你披荆斩棘，而你会为老爸泡茶、捶背，你就是老爸的贴心小棉袄！'][randomNum(0,1)];break;
