                        case this.score>=81 && this.score<=95: this.by = ['坑到你爸，不想做你爸!','女大十八变，越变越随便。' , 'No Keng Dad No Die'][randomNum(0,2)];this.wa = ['都说女孩斯文安静，但你用实际行动推翻了这些刻板印象！爬树、掏鸟、打架，总有一样是你擅长的！但不管怎样，老爸的目光却永远温柔！','你爹大概觉得，自己是你从路边捡回来的，毕竟在家里毫无地位！毫无话语权！不仅怕老婆，还是个女儿奴！被你坑成这样，还是满满的宠爱！'][randomNum(0,1)];break;
                        case this.score>=96 && this.score<=100:this.by = ['老爸被你坑到崩溃！' , '你这么坑爹，良心不会痛吗！','你爹被坑到无FUCK说！'][randomNum(0,2)];this.wa = ['都说女孩斯文安静，但你用实际行动推翻了这些刻板印象！爬树、掏鸟、打架，总有一样是你擅长的！但不管怎样，老爸的目光却永远温柔！','你爹大概觉得，自己是你从路边捡回来的，毕竟在家里毫无地位！毫无话语权！不仅怕老婆，还是个女儿奴！被你坑成这样，还是满满的宠爱！'][randomNum(0,1)];break;
                    }
                }else{

                    switch(true){
                        case this.score>=0 && this.score<=7:    this.by = ['老爸得感谢你不坑之恩！','明人不挖坑，尤其对亲爹！','只坑队友，不坑爹！'][randomNum(0,2)];this.wa = ['善良如你，看到爸爸下班后的疲惫，你暗自发誓要为他分担。两父子都是会为了家庭，褪去身上的棱角，真棒！','善良如你，看到爸爸下班后的疲惫，你暗自发誓要为他分担。两父子都是会为了家庭，褪去身上的棱角，真棒！'][randomNum(0,1)];break;
                        case this.score>=8 && this.score<=20:   this.by = ['坑爹？不存在的！','坑谁都行，就不坑爹','对于坑爹，你还是节制的！'][randomNum(0,2)];this.wa = ['善良如你，看到爸爸下班后的疲惫，你暗自发誓要为他分担。两父子都是会为了家庭，褪去身上的棱角，真棒！','善良如你，看到爸爸下班后的疲惫，你暗自发誓要为他分担。两父子都是会为了家庭，褪去身上的棱角，真棒！'][randomNum(0,1)];break;
                        case this.score>=21 && this.score<=40:  this.by = ['亲爹就是用来坑的！','佛系如我，随缘坑爹！', '你和老爸坑人能力相当！'][randomNum(0,2)];   this.wa = ['你是一个内心传统，观念新颖的小哥哥，既有冷静的一面，也有热情的一面。虽然偶尔坑爹，那也是因为事情不在你的掌控里！','你是一个温文尔雅、体贴入微的绅士，你会把家庭放在第一，认为百善孝为先。坑爹对你来说，就是和老爸增加感情的一种方式，你们开心就好！'][randomNum(0,1)];break;
