                        case this.score>=61 && this.score<=80:  this.by = ['专坑亲爹的小能手,' , '荣获坑爹比赛亚军' , '日常坑爹，周末开挂！'][randomNum(0,2)]; this.wa = ['你从小就是一个坑爹的熊孩子，这种能力大概是从你爸身上继承的！你特别喜欢捉弄人，常常挖坑给别人跳，即便是自己的老爸，你也不会手软！','如果不坑爹，那还是亲儿子吗？反正坑不坑爹，你也少不了一顿揍！你的聪明才智，大概都用来对付亲爹了，你坑爹的事例，估计可以写一套坑爹攻略了！'][randomNum(0,1)];break;
                        case this.score>=81 && this.score<=95:  this.by = ['坑爹你第二，没人敢称霸！' , '老爸被你坑到石化！' , '坑爹你最稳！'][randomNum(0,2)]; this.wa = ['你坑爹的技术非常高，很多时候你爸都没发觉被你坑了！这或许因为你有非常高的情商，总能在最后一刻化险为夷！','你坑爹的技术非常高，很多时候你爸都没发觉被你坑了！这或许因为你有非常高的情商，总能在最后一刻化险为夷！'][randomNum(0,1)];break;
                        case this.score>=96 && this.score<=100: this.by = ['坑爹之神，爸爸心好疼！','大吉大利，今晚坑爹！', '挖掘机专业优先毕业生'][randomNum(0,2)]; this.wa = ['你坑爹的技术非常高，很多时候你爸都没发觉被你坑了！这或许因为你有非常高的情商，总能在最后一刻化险为夷！','你坑爹的技术非常高，很多时候你爸都没发觉被你坑了！这或许因为你有非常高的情商，总能在最后一刻化险为夷！'][randomNum(0,1)];break;
                    }
                }
            }
        },
        components:{
            Button
        },
        mounted(){
            this.getWA()
            this.getRGBA()
            this.getdw()

            const title = '经鉴定【'+ this.name + '】坑爹段位为【' + this.dw +'】，测过才知道你坑不坑！'

            const share = new shareTool({pic:'https://webres.psy-1.com/sgy/dist/static/img/f-day_img_openning-daddy@3x.1695a13.png', url:window.location.href.split('#')[0], title:title, desc:'快来测一测你的坑爹指数！'})
            if(this.$store.getters.isApp)  share.appInit()
            if(this.$store.getters.isWeiXin)share.weiXinShare()
            this.changeToimg()
        }
    }
</script>

