<template>
  <div class="home">
    <img class="logo" src="../images/f-day_img_openning-logo.png" />
    <div class="top">
      <img class="title-img" src="../images/f-day_img_openning-bigtitle.png" />
      <img class="center-img" src="../images/f-day_img_openning-daddy.png" />
    </div>

    <Button @click.native="toPlayPage" class="button">开始测试</Button>
    <a id="dl"  target="view_window" href="https://a.app.qq.com/o/simple.jsp?pkgname=com.psyone.brainmusic">
      <img class="bottom-download" src="@/images/f-day_img_openning-adv.png"/>
    </a>
  </div>
