<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import Button from '@/components/button.vue'
export default {
  name: 'home',
  components: {
    HelloWorld,
    Button
  },
  methods: {
      toPlayPage(){


          if(document.getElementById('myaudio').paused && !localStorage.getItem('fuckyou')){
              this.$parent.fuck()
          }


          if(localStorage.getItem('name')){
              this.$router.push({path: '/playpage'})
          }else{
              this.$router.push({path: '/input'})
          }

      }
  },
    mounted(){
        if(this.$store.getters.isApp) {
            document.querySelector('#dl').style.display = 'none'
        }
    }
}
</script>

